.header {
  position: fixed;
  top: 0;
  left: 0;
  height: 80px;
}
.aside {
  position: relative;
  float: left;
  width: 1rem;
  height: 10rem;
  margin-top: 80px;
}
.text {
  width: 100px;
  height: 100px;
  border: 1px solid green;
}
.main {
  position: relative;
  height: 10rem;
  padding-top: 80px;
  margin-left: 1rem;
  display: flex;
}
.main .phone {
  width: 375px;
  height: 667px;
  margin: 0 auto;
  border: 1px solid red;
  overflow: auto;
}
